import { _decorator,Component, director,Node} from 'cc';
import { GameUILayers, GameUILayerNames } from '../scripts/GameUILayers';
import { HotUpdateEvent } from '../scripts/hotupdate/HotUpdateEvent';
import { HotUpdateCtrl } from '../scripts/hotupdate/HotUpdateCtrl';
import { GameConst } from '../scripts/GameConst';
import { game } from 'cc';
import { JSB, NATIVE } from 'cc/env';
import { EasyControllerEvent } from '../core_tgx/easy_controller/EasyController';
import { Label } from 'cc';

const { ccclass, property } = _decorator;


@ccclass('HotUpdate')
export class Start extends Component {

    @property(Label)
    txtLoading: Label;

    @property(Node)
    loadingBar: Node;

    hotupdateCtrl: HotUpdateCtrl = null
    async start() {
        if (!NATIVE || !JSB || !GameConst.hotUpdate_Open) {
            this._enterHall()
            return
        }
        this.loadingBar.setScale(0, 1, 1);
        director.on(HotUpdateEvent.HotUpdateRate, this.onHotUpdateRate, this);
        director.on(HotUpdateEvent.HotUpdateFinish, this.onHotUpdateFinish, this);
        director.on(HotUpdateEvent.CheckVersionResult, this.onCheckVersionResult, this);

        this.hotupdateCtrl = new HotUpdateCtrl()
        await this.hotupdateCtrl.init("main")
        this.hotupdateCtrl.checkUpdate()
    }
    /**
     * 
     */
    onCheckVersionResult(name: string, newVersionFoud: boolean) {
        console.log(`CheckVersionResult: ${name}, ${newVersionFoud}`)
        if (newVersionFoud) {
            this.hotupdateCtrl && this.hotupdateCtrl.hotUpdate();
        } else {
            this._enterHall();
        }
    }

    /**
     * 
     */
    onHotUpdateRate(name:string,rate: number) {
        this.txtLoading.string = Math.floor(rate*100)+"%";
        this.loadingBar.setScale(rate, 1, 1);
    }

    /**
     * 
     */
    onHotUpdateFinish(name: string, success: boolean) {
        if (success) {
            console.log("========onHotUpdateSuccess======", name)
            setTimeout(() => {
                game.restart();
            }, 200)
        } else {  // 
            // this._enterHall()
            console.log("========onHotUpdateFail======")
        }
    }

    update(deltaTime: number) {
    }

    private _enterHall() {
        console.log("enterGame")
        if(this.hotupdateCtrl)
        {
            //
            let version = this.hotupdateCtrl.getLocalManifestVersion();
            console.log("mainVersion:",version);
            HotUpdateCtrl.mainVersion = version;
        }
        director.loadScene("start");
    }

    protected onDestroy(): void {
        director.targetOff(this)
        this.hotupdateCtrl && this.hotupdateCtrl.destory();
        director.on(HotUpdateEvent.HotUpdateRate, this.onHotUpdateRate, this);
        director.on(HotUpdateEvent.HotUpdateFinish, this.onHotUpdateFinish, this);
        director.on(HotUpdateEvent.CheckVersionResult, this.onCheckVersionResult, this);
    }
}



