import { _decorator, Component, Node } from 'cc';
import { instantiate } from 'cc';
import { Prefab } from 'cc';
import { ResourceMgr } from '../../base/ResourceMgr';
import { ModuleDef } from 'db://assets/scripts/ModuleDef';
import { UIMgr } from '../UIMgr';
import { Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('UITip')
export class UITip {
    private _tips: Array<any> = [];

    private static _instance: UITip = null;
    public static get instance() {
        if (!this._instance) {
            this._instance = new UITip();
        }
        return this._instance;
    }

    public show(str: string) {
        ResourceMgr.inst.loadResByModuleBundle(ModuleDef.BASIC, 'ui_tip/Tip', Prefab, (err, prefab: Prefab) => {
            const panel = instantiate(prefab);
            panel.parent = UIMgr.inst.getLayerNode(-1);
            this.createrTip(panel, str);
        });

    }

    private createrTip(tipNode: Node, str: string) {
        const tip: any = tipNode.getComponent("UITipPanel");
        tip.tipStr = str;
        //
        this._tips.push(tip);
        tip.node.once('over', () => {
            tip.node.removeFromParent();
            tip.node.destroy();
            this._tips.shift();
            this.queue();
        });
        tip.node.once('queue', () => {
            this.queue();
        });
        this.queue();
    }

    private queue(i = 0) {
        if (this._tips.length == 0) return;
        let tip = this._tips[i];
        if (!tip) return;
        if (!tip?.isMove) {
            tip.node.parent = UIMgr.inst.getLayerNode(-1);
            tip.init();
            tip.tip(tip.tipStr, 1);
        } else {
            if (tip.isQueue) {
                this.queue(i + 1);
            }
        }
    }
}


