import { UITransform } from 'cc';
import { screen } from 'cc';
import { view } from 'cc';


import { _decorator, Component, Node, Label } from 'cc';
import { UIMgr } from '../easy_ui_framework/UIMgr';
import { sys } from 'cc';
import { Vec3 } from 'cc';
import { macro } from 'cc';
import { ResolutionPolicy } from 'cc';
import { View } from 'cc';
import { math } from 'cc';
import { Widget } from 'cc';
import { find } from 'cc';
import { Canvas } from 'cc';
import { EnvMgr } from './EnvMgr';
import { CCBoolean } from 'cc';
import { native } from 'cc';
import { JSB } from 'cc/env';
import { director } from 'cc';
import { GameConst } from '../../scripts/GameConst';
const { ccclass, property } = _decorator;



@ccclass('PortraitMgr')
export default class PortraitMgr extends Component {
    @property({ tooltip: 'UIsolo' })
    isOnlyUI = false;
    onLoad() {

        // pc web
        if (sys.isBrowser && !sys.isMobile && !GameConst.isShowWebPortrait) {
            if (!this.isOnlyUI) {
                var designSize: math.Size = view.getDesignResolutionSize();
                view.setDesignResolutionSize(designSize.height, designSize.width, ResolutionPolicy.SHOW_ALL);
                this.onWindowResizePC();
                //bugsetDesignResolutionSize 
                view.emit('canvas-resize');
            }
            else {
                this.onWindowResizePC();
            }

        }
        else {

            if (!this.isOnlyUI) {
                if (sys.os == sys.OS.ANDROID) {
                    native.reflection.callStaticMethod('com/cocos/game/AppActivity', 'setOrientation', '(Ljava/lang/String;)V', "V");

                } else if (sys.os == sys.OS.IOS) {

                }
                view.setOrientation(macro.ORIENTATION_PORTRAIT);
                var designSize: math.Size = view.getDesignResolutionSize();
                view.setDesignResolutionSize(designSize.height, designSize.width, ResolutionPolicy.SHOW_ALL);
                this.onWindowResizePC();
                let node = director.getScene()?.getChildByName('Canvas');
                const widget = node.getComponent(Widget);
                if (widget) {
                    widget.updateAlignment();
                }

            }
            else {
                this.onWindowResizePC();
            }
        }


    }
    async start() {



    }
    onDisable() {
        // pc web
        if (sys.isBrowser && !sys.isMobile && !GameConst.isShowWebPortrait) {
            if (!this.isOnlyUI) {
                var designSize: math.Size = view.getDesignResolutionSize();
                view.setDesignResolutionSize(designSize.height, designSize.width, ResolutionPolicy.SHOW_ALL);
                //bugsetDesignResolutionSize 
                view.emit('canvas-resize');
            }
        }
        else {
            if (!this.isOnlyUI) {
                if (sys.os == sys.OS.ANDROID) {
                    native.reflection.callStaticMethod('com/cocos/game/AppActivity', 'setOrientation', '(Ljava/lang/String;)V', "H");
                } else if (sys.os == sys.OS.IOS) {

                }
                view.setOrientation(macro.ORIENTATION_LANDSCAPE);
                var designSize: math.Size = view.getDesignResolutionSize();
                view.setDesignResolutionSize(designSize.height, designSize.width, ResolutionPolicy.SHOW_ALL);
            }

        }
    }
    private onWindowResizePC() {
        var widget = this.node.getComponent(Widget);
        if (!widget) {
            widget = this.node.addComponent(Widget);
        }
        //  Widget 
        widget.isAlignTop = true;
        widget.isAlignBottom = true;
        widget.isAlignLeft = true;
        widget.isAlignRight = true;

        widget.top = 0;
        widget.bottom = 0;
        widget.left = 0;
        widget.right = 0;

        //  Widget 
        widget.updateAlignment();
    }

}
