//AudioMgr.ts
import { Node, AudioSource, AudioClip, resources, director, assetManager } from 'cc';
import { ResourceMgr } from './ResourceMgr';
import { AudioManager } from './audio/AudioManager';
/**
 * @en
 * this is a sington class for audio play, can be easily called from anywhere in you project.
 * @zh
 * 
 */
export class AudioMgr {

    private static _inst: AudioMgr;
    public static get inst(): AudioMgr {
        if (this._inst == null) {
            this._inst = new AudioMgr();
        }
        return this._inst;
    }

    private _musicSource: AudioSource;
    private _soundSource: AudioSource;
    private _musicVolume = 1.0;
    private _soundVolume = 1.0;

    public audioMgr: Node;
    public audio: AudioManager;
    constructor() {
        //@en create a node as audioMgr
        //@zh  audioMgr
        this.audioMgr = new Node();
        this.audioMgr.name = '__audioMgr__';

        //@en add to the scene.
        //@zh 
        director.getScene().addChild(this.audioMgr);

        //@en make it as a persistent node, so it won't be destroied when scene change.
        //@zh 
        director.addPersistRootNode(this.audioMgr);

        // //@en use to play background music.
        // //@zh 
        // let music = new Node();
        // this.audioMgr.addChild(music);
        // this._musicSource = music.addComponent(AudioSource);

        // //@en use to play sound.
        // //@zh 
        // let sound = new Node();
        // this.audioMgr.addChild(sound);
        // this._soundSource = sound.addComponent(AudioSource);
        this.audio = this.audioMgr.addComponent(AudioManager);
        this.audio.load();
    }

    // public get audioSource() {
    //     return this._musicSource;
    // }

    // public set musicVolume(v: number) {
    //     this._musicVolume = v;
    //     this._musicSource.volume = this._musicVolume;
    // }

    // public set soundVolume(v: number) {
    //     this._soundVolume = v;
    //     this._soundSource.volume = this.soundVolume;
    // }
    /** */
    private _switchMusic: boolean = true;
    public get switchMusic(): boolean {
        return this._switchMusic;
    }
    public set switchMusic(v: boolean) {
        if (this._switchMusic != v) {
            if (v == true) {
                this.audio.music.play();
            } else {
                this.audio.music.pause();
            }
        }
        this._switchMusic = v;
        this.audio.switchMusic = v;
        this.audio.save();
    }
    /** */
    private _switchEffect: boolean = true;
    public get switchEffect(): boolean {
        return this._switchEffect;
    }
    public set switchEffect(v: boolean) {
        if (this._switchEffect != v) {
            if (v == true) {
                this.audio.effect.play();
            } else {
                this.audio.effect.pause();
            }
        }
        this._switchEffect = v;
        this.audio.switchEffect = v;
        this.audio.save();
    }
    /** */
    private _switch: boolean = true;
    public get switch(): boolean {
        return this._switch;
    }
    public set switch(v: boolean) {
        if (this._switch != v) {
            if (v == true) {
                this.resume();
            } else {
                this.pause();
            }
        }
        this._switch = v;
        this.audio.switchMusic = v;
        this.audio.switchEffect = v;

        this.audio.save();
    }
    /** */
    private _volumeMusic: number = 1;
    public get volumeMusic(): number {
        return this._volumeMusic;
    }
    public set volumeMusic(v: number) {
        this._volumeMusic = v;
        this.audio.music.volume = v;
        this.audio.save();
    }
    /** */
    private _volumeEffect: number = 1;
    public get volumeEffect(): number {
        return this._volumeEffect;
    }
    public set volumeEffect(v: number) {
        this._volumeEffect = v;
        this.audio.effect.volume = v;
        this.audio.save();
    }
    /** */
    private _volume: number = 1;
    public get volume(): number {
        return this._volume;
    }
    public set volume(v: number) {
        this._volume = v;
        this.audio.music.volume = v;
        this.audio.effect.volume = v;
        this.audio.save();
    }
    /** */
    changVolume(volumeMusic: number, volumeEffect: number) {
        this.audio.music.volume = volumeMusic;
        this.audio.effect.volume = volumeEffect;
        this.audio.save();
    }
    /**
     * @en
     * play short audio, such as strikes,explosions
     * @zh
     * , 
     * @param sound clip or url for the audio
     * @param volume 
     */
    async playOneShot(sound: AudioClip | string, volume: number = 1.0, bundleName: string = 'resources') {
        // if (sound instanceof AudioClip) {
        //     this._soundSource.volume = 1.0;
        //     this._soundSource.playOneShot(sound, volume * this._soundVolume);
        // }
        // else {

        //     let bundle = assetManager.getBundle(bundleName);
        //     bundle.load(sound, (err, clip: AudioClip) => {
        //         if (err) {
        //             console.log(err);
        //         }
        //         else {
        //             this._soundSource.volume = 1.0;
        //             this._soundSource.playOneShot(clip, volume * this._soundVolume);
        //         }
        //     });
        // }
        const aeid = await this.audio.playEffect(sound, bundleName);
        return aeid;
    }

    /**
     * @en
     * play long audio, such as the bg music
     * @zh
     *  
     * @param music AudioClip or url for the music
     * @param volume 
     */
    play(music: string, volume: number = 1.0, bundleName: string = 'resources') {
        // if (music instanceof AudioClip) {
        //     this._musicSource.stop();
        //     this._musicSource.clip = music;
        //     this._musicSource.play();
        //     this._musicSource.volume = this._musicVolume * volume;
        // }
        // else {
        //     let bundle = assetManager.getBundle(bundleName);
        //     bundle.load(music, (err, clip: AudioClip) => {
        //         if (err) {
        //             console.log(err);
        //         }
        //         else {
        //             this._musicSource.stop();
        //             this._musicSource.clip = clip;
        //             this._musicSource.play();
        //             this._musicSource.volume = this._musicVolume * volume;
        //         }
        //     });
        // }
        this.audio.playMusicLoop(music, bundleName)
    }

    /**
     * stop the audio play
     */
    stop() {
        // this._musicSource.stop();
        // this._soundSource.stop();
        this.audio.music.stop();
        this.audio.effect.stop();
    }

    /**
     * pause the audio play
     */
    pause() {
        // this._musicSource.pause();
        // this._soundSource.pause();
        this.audio.music.pause();
        this.audio.effect.pause();
    }

    /**
     * resume the audio play
     */
    resume() {
        // this._musicSource.play();
        // this._soundSource.play();
        this.audio.music.play();
        this.audio.effect.play();
    }

    /**
     * 
     */
    stopOneShot(aeid: number, sound: AudioClip | string, bundleName: string = 'resources') {
        // this.audio.stopEffect(aeid, sound, bundleName);
        this.audio.putEffect(aeid, sound, bundleName);
    }
}
