import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

//ui
export class toolUI {

    /**
     * 
     * @param node 
     * @param callback 
     */
    public static  traverseAllChildren(node: Node, callback: (child: Node) => void) {
        // 
        callback(node);
    
        // 
        const children = node.children;
        for (let i = 0; i < children.length; i++) {
            const child = children[i];
            // 
            toolUI.traverseAllChildren(child, callback);
        }
    }
}


