export class URLUtils{
    public static urlParse(url:string) {
        var params = {};
        if (!url) {
            return params;
        }
        var name, value;
        var str = url; //
        var num = str.indexOf("?")
        str = str.substring(num + 1); //   stringvar.substr(start [, length ]
    
        var arr = str.split("&"); //
        for (var i = 0; i < arr.length; i++) {
            num = arr[i].indexOf("=");
            if (num > 0) {
                name = arr[i].substring(0, num);
                value = arr[i].substr(num + 1);
                params[name] = value;
            }
        }
        return params;
    }
}