import { loader, Constructor, resources, Asset, assetManager, AssetManager } from "cc";
import { GameConst } from "../../scripts/GameConst";

class ResItem {
    public url: string;
    public isLoading = false;
    public callbackArr = [];
}

export class ResourceMgr {
    private static _inst: ResourceMgr = null;
    public static get inst(): ResourceMgr {
        if (!this._inst) {
            this._inst = new ResourceMgr();
        }
        return this._inst;
    }

    private loadingQueue:[] = [];

    public loadRes<T>(url: string, type: any, callback: (err, assets: T) => void) {
        let cache = resources.get(url,type) as any;
        if(cache){
            if(callback){
                setTimeout(()=>{
                    callback(null,cache);
                },10);
            }
            return;
        }
        let loadingItem:ResItem = this.loadingQueue[url];
        if(!loadingItem){
            loadingItem = this.loadingQueue[url] = new ResItem();
            loadingItem.url = url;
        }
        loadingItem.callbackArr.push(callback);
        if(!loadingItem.isLoading){
            loadingItem.isLoading = true;
            resources.load(url, type, (err,asset:Asset)=>{
                delete this.loadingQueue[url];
                for(let k in loadingItem.callbackArr){
                    let cb = loadingItem.callbackArr[k];
                    if(cb){
                        cb(err,asset);
                    }
                }
            });
        }
    }
    /**
     * 
     * 
     **@param {string} moduleName  --ModuleDef
     * @param {string} resPath 
     * @param {*} resType 
     * @return {*}
     * 
     */
    public loadResByModuleBundle<T extends Asset>(moduleName:string,resPath:string,resType:Constructor<T>,onComplete?: ((err: Error | null, data: T) => void) | null):void
    {
        let bundle = assetManager.getBundle(moduleName);
        if(bundle)
        {
            bundle.load(resPath, resType, onComplete);
        }
        else
        {
            if(onComplete) onComplete(new Error("bundle not find:"+moduleName),null);
        }
        
    }
    public releaseByModuleBundle<T extends Asset>(moduleName:string,resPath:string,resType:Constructor<T>):void
    {
        let bundle = assetManager.getBundle(moduleName);
        if(bundle)
        {
            bundle.release(resPath,resType);
        }
        else
        {
            
        }
        
    }
    /**
     * 
     * 
     **@param {string} moduleName  --ModuleDef
     * @param {string} resPath 
     * @param {*} resType 
     * @return {*}
     * 
     */
    public preloadResByModuleBundle(moduleName:string,paths: string | string[], resType: Constructor<Asset> | null, onComplete?: ((err: Error | null, data: any[]) => void) | null): void
    {
        let bundle = assetManager.getBundle(moduleName);
        if(bundle)
        {
            bundle.preload(paths, resType, onComplete);
        }
        else
        {
            if(onComplete)  onComplete(new Error("bundle not find:"+moduleName),null);
        }
    }
    /**
     *   area_config/china/xxx.json xxx.json 
     * @param moduleName
     * @param resPath 
     * @param resType 
     * @param onComplete 
     */
    public loadAreaConfigByModuleBundle<T extends Asset>(moduleName:string,resPath:string,resType:Constructor<T>,onComplete?: ((err: Error | null, data: any) => void) | null):void
    {
        resPath = "area_config/"+GameConst.curArea+"/"+resPath;
        ResourceMgr.inst.loadResByModuleBundle(moduleName,resPath,resType,onComplete);
    }

}