import { _decorator, Component, Node, Vec2, v2  } from 'cc';
const { ccclass, property } = _decorator;

/**
 * 
 */
export class ToolPos  {
    /**
     * 
     * node
     * @param {*} node
     */
    public static localConvertWorldPointAR(node): Vec2
    {
        if (node) {
            return node.convertToWorldSpaceAR( v2(0, 0));
        }
        return null;
    }

    /**
     * 
     * node
     * @param {*} node
     */
    public static localConvertWorldPoint(node): Vec2
    {
        if (node) {
            return node.convertToWorldSpace(v2(0, 0));
        }
        return null;
    }


    /**
     * 
     * node
     * @param {*} node
     * @param {*} worldPoint
     */
    public static worldConvertLocalPointAR(node, worldPoint): Vec2
    {
        if (node) {
            return node.convertToNodeSpaceAR(worldPoint);
        }
        return null;
    }

    /**
     * 
     * node
     * @param {*} node
     * @param {*} worldPoint
     */
    public static worldConvertLocalPoint(node, worldPoint): Vec2
    {
        if (node) {
            return node.convertToNodeSpace(worldPoint);
        }
        return null;
    }

    /**
     * 
     * node
     * @param {*} node
     * @param {*} targetNode
     */
    public static convertOtherNodeSpaceAR(node, targetNode): Vec2
    {
        if (!node || !targetNode) {
            return null;
        }
        //
        let worldPoint = this.localConvertWorldPointAR(node);
        return this.worldConvertLocalPointAR(targetNode, worldPoint);
    }

    /**
     * 
     * node
     * @param {*} node
     * @param {*} targetNode
     */
    public static convertOtherNodeSpace(node, targetNode): Vec2
    {
        if (!node || !targetNode) {
            return null;
        }
        //
        let worldPoint = this.localConvertWorldPoint(node);
        return this.worldConvertLocalPoint(targetNode, worldPoint);
    }
}


