import { ccenum } from 'cc';
import { SpriteFrame } from 'cc';
import { Vec3 } from 'cc';
import { color } from 'cc';
import { tween } from 'cc';
import { Sprite } from 'cc';
import { Button } from 'cc';
import { _decorator, Component, Node } from 'cc';
const { ccclass, property, menu } = _decorator;

enum EButtonType {
    DARK,
    GRAY
}
ccenum(EButtonType);

/**
 * 
 */
@ccclass('ExtendButton')
@menu('/UI/')
export class ExtendButton extends Button {
    @property
    isScale: boolean = true;
    @property({ type: EButtonType, displayName: "" })
    interactableType = EButtonType.DARK;
    @property
    dark = 125;
    @property({ displayName: '', tooltip: '0  1' })
    isToggle: boolean = false;

    private _isChecked: boolean = true;
    @property({ displayName: '', tooltip: '', visible: function () { return this.isToggle; } })
    get isChecked() { return this._isChecked }
    set isChecked(v: boolean) {
        this._isChecked = v
        if (!this.isSelfNode) this.selfSprite.spriteFrame = this.isChecked ? this.toggleFrames[0] : this.toggleFrames[1];
        else this.node.getComponent(Sprite).spriteFrame = this.isChecked ? this.toggleFrames[0] : this.toggleFrames[1];
    }
    @property({ type: [SpriteFrame], tooltip: '', visible: function () { return this.isToggle; } })
    toggleFrames: SpriteFrame[] = [];

    @property({ tooltip: '', visible: function () { return this.isToggle; } })
    isSelfNode: boolean = true;
    
    @property({ type: Sprite, tooltip: '', visible: function () { return !this.isSelfNode; } })
    selfSprite: Sprite = null;

    private _st: Sprite;
    protected onLoad(): void {
        this._st = this.node.getComponent(Sprite);
        this.node.on(Button.EventType.CLICK, () => {
            if (!this.interactable) return;
            // root.audio.play('Click');
            if (this.isToggle) {
                this.isChecked = !this.isChecked;
                if (!this._st) {
                    this._st = this.node.getComponent(Sprite);
                }

                if (!this.isSelfNode) this.selfSprite.spriteFrame = this.isChecked ? this.toggleFrames[0] : this.toggleFrames[1];
                else this._st.spriteFrame = this.isChecked ? this.toggleFrames[0] : this.toggleFrames[1];
            }
            if (this.transition == 0 && this.isScale) {
                tween(this.node).to(.1, { scale: new Vec3(1.05, 1.05, 1.05) }).to(.1, { scale: Vec3.ONE }).start();
            }
        });
    }

    public get interactable() {
        return this._interactable;
    }
    public set interactable(v: boolean) {
        if (!this._st) this._st = this.node.getComponent(Sprite);
        if (this._st) {
            if (this.interactableType == EButtonType.DARK) {
                const c = v ? 255 : this.dark;
                this._st.color = color(c, c, c, 255);
            }
            if (this.interactableType == EButtonType.GRAY) {
                this._st.grayscale = !v;
            }
        }
        this._interactable = v;
    }
}


