import { SpriteFrame } from "cc";
import { UIController } from "../UIController";
import { UIMgr } from "../UIMgr";

import { _decorator, Button, Component, Label, Node } from 'cc';
import { Sprite } from "cc";
import { Color } from "cc";
import { RichText } from "cc";
const { ccclass, property } = _decorator;

@ccclass('tgxLayout_UIAlert')
export class Layout_UIAlert extends Component {

    @property(Label)
    title: Label;

    @property(Label)
    content: Label;
    @property(RichText)
    contentRich: RichText;

    @property(Button)
    btnOK: Button;

    @property(Button)
    btnClose: Button;

    @property(Button)
    btnCancel: Button;

    @property({ type: Sprite, tooltip: "" })
    bg: Sprite = null;
    @property({ type: Sprite, tooltip: "" })
    titleBg: Sprite = null;
    @property({ type: Sprite, tooltip: "" })
    contentBg: Sprite = null;
}

export class UIAlertOptions {
    private _title?: string;
    private _content?: string;
    private _showCancel?: boolean;
    private _cbClick?: Function;
    private _cbClickThisArg?: Function;

    setTitle(title: string): UIAlertOptions {
        this._title = title;
        return this;
    }

    onClick(cb: (isOK: boolean) => void, thisArg?: any): UIAlertOptions {
        this._cbClick = cb;
        this._cbClickThisArg = thisArg;
        return this;
    }
}

export class UIAlert extends UIController {
    private _options: UIAlertOptions;

    /**
     * ()()
     * @param content 
     * @param showCancel (true)
     * @param bgColorHEX 
     * @param titleBgColorHEX 
     * @param contentColorHEX 
     * @param closeColorHEX 
     * @returns 
     */
    public static show(content: string, showCancel?: boolean, bgColorHEX: string = "E4D3FF",
        titleBgColorHEX: string = "#6043D7",
        contentColorHEX: string = "D3CCEB",
        closeColorHEX: string = 'DF54DF'): UIAlertOptions {
        let opts = new UIAlertOptions() as any;
        opts._content = content;
        opts._showCancel = showCancel;

        const bgColor = new Color().fromHEX("#" + bgColorHEX.replace("#", ''));
        const titleBgColor = new Color().fromHEX("#" + titleBgColorHEX.replace("#", ''));
        const contentColor = new Color().fromHEX("#" + contentColorHEX.replace("#", ''));
        const closeColor = new Color().fromHEX("#" + closeColorHEX.replace("#", ''));
        UIMgr.inst.showUI(UIAlert, (alert: UIAlert) => {
            alert.layout.bg.color = bgColor;
            alert.layout.titleBg.color = titleBgColor;
            alert.layout.btnOK.node.getComponent(Sprite).color = titleBgColor;  
            alert.layout.btnClose.node.getComponent(Sprite).color = closeColor;
            alert.layout.btnCancel.node.getComponent(Sprite).color = closeColor;
            alert.layout.contentBg.color = contentColor;

            alert.init(opts);
        }) as UIAlert;
        return opts;
    }

    public get layout(): Layout_UIAlert {
        return this._layout as Layout_UIAlert;
    }

    private init(opts: UIAlertOptions) {
        this._options = opts;
        let options = this._options as any as { _title: string, _content: string, _showCancel: boolean };
        let layout = this._layout as Layout_UIAlert;
        if (options.hasOwnProperty('title')) {
            layout.title.string = options._title || '';
        }

        // layout.content.string = options._content || '';
        layout.contentRich.string = options._content || '';
        layout.btnClose.node.active = options._showCancel;
        layout.btnCancel.node.active = options._showCancel;
        if (!options._showCancel) {
            let pos = layout.btnOK.node.position;
            layout.btnOK.node.setPosition(0, pos.y, pos.z);
        }
    }

    protected onCreated(): void {
        let layout = this._layout as Layout_UIAlert;
        this.onButtonEvent(layout.btnOK, () => {
            this.close();
            let options = this._options as any as { _cbClick: Function, _cbClickThisArg: any };
            if (options._cbClick) {
                options._cbClick.call(options._cbClickThisArg, true);
            }
        });

        this.onButtonEvent(layout.btnCancel, () => {
            this.close();
            let options = this._options as any as { _cbClick: Function, _cbClickThisArg: any };
            if (options._cbClick) {
                options._cbClick.call(options._cbClickThisArg, false);
            }
        });

        this.onButtonEvent(layout.btnClose, () => {
            this.close();
            let options = this._options as any as { _cbClick: Function, _cbClickThisArg: any };
            if (options._cbClick) {
                options._cbClick.call(options._cbClickThisArg, false);
            }
        });
    }
}