import { Vec3 } from "cc";

/**
 * 
 */
export class Num {
    /**
     * number(TSnumberfloat)
     * @param strNum 
     */
    public static parse(strNum: string | number): number {
        let type = typeof (strNum);
        if (type == "string")
            return Number(strNum).valueOf();
        else
            return Math.floor(Number(strNum));
    }


    /**
     * 
     * @param num1 1
     * @param num2 2
     */
    public static max(num1, num2) {
        return Math.max(num1, num2);
    }

    /**
     * 
     * @param exchangeRate 
     * @param val          
     * @returns 
     */
    public static exchangeToThousands(exchangeRate: number, val: number): string {
        val = val / exchangeRate;
        if (!isNaN(val)) {
            var source = val.toFixed(2).split("."); //2
            source[0] = source[0].replace(new RegExp('(\\d)(?=(\\d{3})+$)', 'ig'), "$1.");//
            let strArr: string[] = source[0].split(".");
            let str: string = strArr.join(",") + "." + source[1];
            return str;
        }
        else {
            return "";
        }
    }
    
    public static exchangeToThousands2(exchangeRate: number, val: number): string {
        val = val / exchangeRate;
        if (!isNaN(val)) {
            var source = (val + "").split("."); //2
            source[0] = source[0].replace(new RegExp('(\\d)(?=(\\d{3})+$)', 'ig'), "$1.");//
            let strArr: string[] = source[0].split(".");
            let str: string;
            if (source.length > 1) {
                str = strArr.join(",") + "." + source[1];
            } else {
                str = strArr.join(",");
            }
            return str;
        }
        else {
            return "";
        }
    }

    public static toThousandsBack(str: string): number {
        let tempArr: string[] = str.split(","); //2
        let num: number = Number(tempArr.join(""));
        return num;
    }

    public static fixNum(exchangeRate: number, val: number): string {
        let res = "";
        let n: number = val / exchangeRate;
        res = n.toFixed(2);
        return res;
    };

    /**t*/
    public static twoBezier(p0: Vec3, p1: Vec3, p2: Vec3, t: number): Vec3 {
        let x: number = Math.pow((1 - t), 2) * p0.x + 2 * t * (1 - t) * p1.x + Math.pow(t, 2) * p2.x;
        let y: number = Math.pow((1 - t), 2) * p0.y + 2 * t * (1 - t) * p1.y + Math.pow(t, 2) * p2.y;
        return new Vec3(x, y);
    }

    /**t*/
    public static threeBezier(p0: Vec3, p1: Vec3, p2: Vec3, p3: Vec3, t: number): Vec3 {
        let x: number = Math.pow((1 - t), 3) * p0.x + 3 * t * Math.pow((1 - t), 2) * p1.x + 3 * Math.pow(t, 2) * (1 - t) * p2.x + Math.pow(t, 3) * p3.x;
        let y: number = Math.pow((1 - t), 3) * p0.y + 3 * t * Math.pow((1 - t), 2) * p1.y + 3 * Math.pow(t, 2) * (1 - t) * p2.y + Math.pow(t, 3) * p3.y;
        return new Vec3(x, y);
    }
}
