import { Num } from "./Num";


export class Str 
{
    /**
     * format,abc{0}{1}{2} 012paramArr
     * @param content 
     * @param paramArr content
     */
    public static format(content: string, ...paramArr: Array<any>): string
    {                
        for (let i = 0; i < paramArr.length; ++i)
        {
            let reg = Str.getFormatReg(i)       //
            content = content.replace(reg, paramArr[i].toString());
        }
        return content;
    }

    //
    private static getFormatReg(i: number): RegExp
    {
        switch (i)
        {
            case 0: return /\{0\}/g;
            case 1: return /\{1\}/g;
            case 2: return /\{2\}/g;
            case 3: return /\{3\}/g;
            case 4: return /\{4\}/g;
            case 5: return /\{5\}/g;
            case 6: return /\{6\}/g;
            case 7: return /\{7\}/g;
            case 8: return /\{8\}/g;
            case 9: return /\{9\}/g;
            case 10: return /\{10\}/g;
            case 11: return /\{11\}/g;
            case 12: return /\{12\}/g;
        }
        return null;
    }

    /**
     * 
     * @param str 
     * @param sep 
     */
    public static split(str: string, sep: string): string[]
    {
        return str.split(sep);
    }

    /**
     * 
     * @param len 
     */
    public static getRandStr(len: number)
    {
        len = len || 32;
        var $chars = 'ABCDEFGHIJKLMNOPQRSTUCWXYZabcdefghijklmnopqrstuvwxyz12345678p';
        var maxPos = $chars.length;
        var pwd = '';
        for (let i = 0; i < len; i++)
        {
            pwd += $chars.charAt(Math.floor(Math.random() * maxPos));
        }
        return pwd;
    }

    /**
     *  true:
     * @param param 
     */
    public static isEmpty(param: string): boolean
    {
        if(typeof param == "undefined" || param == null) return true;
        
        param = Str.delSpace(param);//
        return param == "";
    }

    /**
     * 
     * @param param
     */
    public static isUrl(param: string): boolean
    {
        if (this.isEmpty(param)) return false;
        return (this.isContain(param, "http"));
    }

    /**
     * 
     * @param param 
     */
    public static delSpace(param: string): string
    {
        return param.replace(/\s+/g, "");
    }

    /**
     * 
     * @param param 
     */
    public static delSpaceLr(param: string): string
    {
        return param.replace(/^\s+|\s+$/g, "");
    }

    /**
     * 
     * @param param 
     */
    public static delRn(param: string): string
    {
        return param.replace(/[\r\n]/g, "");
    }

    /**
     * 2
     * @param money 
     * @param num 
     */
    public static decimal(money: string | number, num: number = 2): string
    {
        let param: number = typeof money == "string" ? parseFloat(money) : money;
        return param.toFixed(num);
    }

    /**
     * float
     * @param str1  1
     * @param str2  2
     * @param num 
     * @returns 
     */
    public static add2(str1: string, str2: string, num: number = 2)
    {
         // 
        const parseNumber = (str: string): number => {
        // 
        const cleanStr = str.replace(/,/g, '');
        // 
        return parseFloat(cleanStr);
       };

    let f1 = parseNumber(str1);
    let f2 = parseNumber(str2);
    
    //  Str.decimal 
    return Str.decimal(f1 + f2, num);
    }

    /**
     * number(TSnumberfloat)
     * @param strNum 
     */
    public static parse(num: number): string
    {
        return num.toString();
    }

    /**
     * url
     * @param url 
     */
    public static encodeURI(url: string): string
    {
        return encodeURI(url);
    }

    /**
     * 
     * @param num 
     */
    public static minus(strNum: string): string
    {
        let num = Num.parse(strNum);
        num = -num;
        return num.toString();
    }

    //ASCII
    public static getASCIINum(str: string): number
    {
        let res = 0;
        let n = str.length;
        for(let i = 0; i < n; ++i )
        {
            let num = str.charCodeAt(0);
            res += num;
        }
        return res;
    }

    /**
     * tring
     * @param str
     * @param subStr
     */
    public static isContain(str: string, subStr: string): boolean
    {
        //return (str.search(subStr) != -1);
        let pos = str.indexOf(subStr);
        if (pos != -1) return true;
        return false;
    }

    /**
     * tring
     * @param str
     * @param subStr
     */
    public static isBeg(str: string, subStr: string): boolean
    {
        return str.startsWith(subStr);
    }


}
